/* inter-regular - latin */
@font-face {
  font-display: swap;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/inter-v8-latin-regular.eot');
  src: local(''),
    url('../fonts/inter-v8-latin-regular.eot?#iefix')
    format('embedded-opentype'),
    url('../fonts/inter-v8-latin-regular.woff2') format('woff2'),
    url('../fonts/inter-v8-latin-regular.woff') format('woff'),
    url('../fonts/inter-v8-latin-regular.ttf') format('truetype'),
    url('../fonts/inter-v8-latin-regular.svg#Inter') format('svg'); /* Legacy iOS */
}

